export default "() => {\n  const renderMonthContent = (month, shortMonth, longMonth) => {\n    const tooltipText = `Tooltip for month: ${longMonth}`;\n    return <span title={tooltipText}>{shortMonth}</span>;\n  };\n  return (\n    <DatePicker\n      selected={new Date()}\n      renderMonthContent={renderMonthContent}\n      showMonthYearPicker\n      dateFormat=\"MM/yyyy\"\n    />\n  );\n};\n";